
html {
  font-family: "Helvetica Neue", Helvetica, STHeiTi, Arial, sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-size: 62.5%;
}
body {
  margin: 0;
  font-size: 1.4rem;
  line-height: 1;
  color: #333333;
  background-color: white;
  height: 100%;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
article, aside, details, figcaption, figure, footer, header, hgroup, main, nav, section, summary {
  display: block;
}
audio, canvas, progress, video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden], template {
  display: none;
}
svg:not(:root) {
  overflow: hidden;
}
a {
  background: transparent;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
  color: #0088cc;
}
a:active {
  outline: 0;
}
a:active {
  color: #006699;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b, strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
  vertical-align: middle;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}
code, kbd, pre, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
img {
  border: none;
  outline: none;
  padding: 0;
  -o-border-image: none;
     border-image: none;
}
button, input, optgroup, select, textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button, select {
  text-transform: none;
}
button, html input[type="button"], input[type="reset"], input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled], html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input {
  line-height: normal;
}
input[type="checkbox"], input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button, input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button, input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  resize: vertical;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td, th {
  padding: 0;
}
h1, h2, h3, h4, h5, h6, p, figure, form, blockquote {
  margin: 0;
}
ul, ol, li, dl, dd {
  margin: 0;
  padding: 0;
}
ul, ol {
  list-style: none outside none;
}
h1, h2, h3 {
  line-height: 2;
  font-weight: normal;
}
h1 {
  font-size: 1.8rem;
}
h2 {
  font-size: 1.6rem;
}
h3 {
  font-size: 1.4rem;
}
input::-moz-placeholder, textarea::-moz-placeholder {
  color: #cccccc;
}
input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #cccccc;
}
input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #cccccc;
}
* {
  box-sizing: border-box;
}
body {
  width: 100%;
  max-width: 1920px;
  margin: auto;
  height: 100%;
  overflow: hidden;
}
html {
  background: #222;
  height: 100%;
}
.home {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.home .loading {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 333;
    color: #fff;
}
.home .loading .mask {
      position: absolute;
      width: 100%;
      height: 100%;
      background: #000;
      opacity: .2;
}
.home .loading .content {
      position: absolute;
      width: 100%;
      height: 100%;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.home .loading .content .loading-img {
        background: url("../../assets/loading.gif") no-repeat;
        width: 1.33333rem;
        height: 1.33333rem;
        background-size: 100% 100%;
}
.home .upload {
    display: block;
    margin: auto;
    width: 3.29333rem;
    height: 2.96rem;
    background: url("../../assets/button.png") 0 0 no-repeat;
    background-size: 100% 100%;
}
.home .info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-top: -1rem;
}
.home .info-user_image {
    margin-left: 0.26667rem;
    width: 3.14667rem;
}
.home .info-user_image img {
      width: 100%;
}
.home .info-user_content {
    padding-left: 0.4rem;
    text-align: left;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    color: #fff;
}
.home .info-user_content p {
      margin: 0.26667rem;
}
.home .info-user_content .key {
      color: #fbebba;
      margin-right: 0.13333rem;
      font-size: 0.4rem;
}
.home .info-user_content .value {
      color: #ffe55d;
      font-size: 0.50667rem;
}



